.quantity {
  display: flex;
  justify-content: center;
  margin-top: 4px;
  width: 175px;

  input {
    height: 24px;
    border: none;
    padding: 0;
    font-size: 14px;
    text-align: center;
    box-shadow: inset 0 0 2px #78716C;
    width: 100%;

    &::-webkit-inner-spin-button {
      appearance: none;
    }

    &:focus {
      outline: none;
    }
